import { Typography } from "@material-ui/core";

interface PerigoZumbi {
  quantidade: number;
  tipo:
    | "abomina√ß√£o"
    | "abomina√ß√£o maculada"
    | "balofo"
    | "lerdo"
    | "lerdo maculado"
    | "corredor"
    | "necromante"
    | "horda";
}

interface ZumbisProps {
  zumbi: PerigoZumbi;
}

export const Zumbis = ({ zumbi }: ZumbisProps) => {
  const nomePlural = () => {
    if (zumbi.tipo === "balofo" || zumbi.tipo === "lerdo") {
      return `${zumbi.tipo}s`;
    }
    return `${zumbi.tipo}es`;
  };

  const nenhumZumbi = () => <>
      <Typography variant="h5" component="h1" align="center">
        n√£o veio nada üçÄüòÅ
      </Typography>
  </>;
  const ativacaoExtra = () => (
    <>
      <Typography variant="h5" component="h1" align="center">
        <strong>nova ativa√ß√£o de: </strong> {nomePlural()} üò±
      </Typography>
    </>
  );

  const ehVazio = () => {
    if (!zumbi) return false;
    return !zumbi.tipo && !zumbi.quantidade;
  };

  const ehAtivacaoExtra = () => {
    if (!zumbi) return false;
    return !zumbi.quantidade && zumbi.tipo;
  };

  const ehNormal = () => {
    if (!zumbi) return false;
    return !!zumbi.quantidade && !!zumbi.tipo;
  };

  return (
    <>
      {!zumbi && <div>Come√ßar?</div>}
      {ehAtivacaoExtra() && ativacaoExtra()}
      {ehVazio() && nenhumZumbi()}
      {ehNormal() && (
        <Typography variant="h5" component="h1" align="center">
          üî• <strong>{zumbi.quantidade}</strong> {zumbi.tipo} üî•
        </Typography>
      )}
    </>
  );
};
